<template>
  <div class="shows-box">
    <h3>Upcoming Shows</h3>
    <ul>
      <li v-for="show in shows" :key="show.date">
        <strong>{{ show.date }}</strong> - {{ show.venue }} ({{ show.city }})
      </li>
    </ul>
  </div>
</template>

<script>
export default {
  name: 'UpcomingShows',
  data() {
    return {
      // The shows array starts empty. It will be filled with data from the database.
      shows: []
    }
  },
  mounted() {
    // This method is called automatically when the component is first created.
    this.fetchShows();
  },
  methods: {
    async fetchShows() {
      try {
        const response = await fetch('http://localhost/band-api/getShows.php');
        const data = await response.json();
        this.shows = data;
      } catch (error) {
        console.error("There was an error fetching the shows:", error);
      }
    }
  }
}
</script>

<style scoped>
.shows-box {
  border: 1px solid #ddd;
  padding: 1rem;
  width: 30%;
  text-align: center;
  background: #fff;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  margin-bottom: 0.5rem;
}
</style>